import React from "react";
import { Form, Input } from "antd";
import PlainSelect from "../../util/PlainSelect";
const { TextArea } = Input;

export default function rfidEditForm() {
  let formItems = {
    label: {},
    code: {
      rules: [
        {
          required: true,
        },
      ],
    },
    type: {
      rules: [
        {
          required: true,
        },
      ],
      label: this.m("typename"),
      component: <PlainSelect labelKey="name" cache query="/card/type" />,
    },
    client: {
      label: this.m("client"),
      component: <PlainSelect labelKey="label" cache query="/client/list" />,
    },

    description: {
      component: <TextArea />,
    },
  };
  return (
    <>
      {Object.entries(formItems).map(([key, item]) => {
        let label = item.label || this.m(key);
        let component = item.component || <Input />;

        return (
          <Form.Item key={key} rules={item.rules} label={label} name={key}>
            {component}
          </Form.Item>
        );
      })}
    </>
  );
}
