import React, {useState, useEffect} from "react";
import Context from "../../Context";
import {withRouter} from "react-router-dom";
import {injectIntl} from "react-intl";
import {Helmet} from "react-helmet";
import {Link} from "react-router-dom";
import {Button, Card, Col, Empty, Row, Spin, Tabs, Avatar, Radio, DatePicker} from "antd";
import CircularProgress from "../../components/CircularProgress";
import {
  ArrowRightOutlined,
  EnvironmentOutlined,
  PlusOutlined,
  RetweetOutlined,
} from "@ant-design/icons";
import paramsHasChanged from "../../util/paramsHasChanged";
import ClientTransactions from "./ClientTransactions";
import ClientStations from "./ClientStations";
import isDate from "../../util/isDate";
import Flex from "../../util/Flex";
import Moment from "moment";
import Statelabel from "../../util/Statelabel";
import ClientEditModal from "./ClientEditModal";
import BalanceEditModal from "./BalanceEditModal";
import clientEditForm from "./clientEditForm";
import balanceEditForm from "./balanceEditForm";
import ClientCard from "./ClientCard";
import ClientOrder from "./ClientOrder";
import ClientInvoise from "./ClientInvoise";
import ClientChart from "./ClientChart";
import ClientAccount from "./ClientAccount";

const {RangePicker} = DatePicker;

class ClientDetail extends React.Component {
  static contextType = Context;

  constructor(props) {
    super(props);

    this.state = {
      pending: false,
      clientId: false,
      clientOrder: false,
      item: {},
      access: {},
      balance: {},
      chart: {},
      amount: {},
      authResult: {},
      editModalVisible: false,
      addModalVisible: false,
      photoModalVisible: false,
    };
  }

  m = (key, prefix = "client.") => {
    const {intl} = this.props;
    return intl.formatMessage({id: prefix + key});
  };

  pendingStart = () => {
    if (this.props.onPendingStart) {
      this.props.onPendingStart();
      return;
    }
    this.setState({pending: true});
  };
  pendingEnd = () => {
    if (this.props.onPendingEnd) {
      this.props.onPendingEnd();
      return;
    }
    this.setState({pending: false});
  };

  componentDidMount() {
    this.setParams();
  }

  componentDidUpdate(prevProps) {
    if (paramsHasChanged(prevProps, this.props)) {
      this.setParams();
    }

    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.load();
    }
  }

  setParams = () => {
    const {param1: clientId} = this.props.match.params;
    this.setState(
      {
        clientId,
      },
      () => this.load()
    );
  };

  load = async () => {
    if (this.state.clientId) {
      this.pendingStart();
      const dateTo = Moment().format("YYYY-MM-DD");
      const dateFrom = Moment().subtract(1, 'month').format("YYYY-MM-DD");
      let authResult = await this.context.api.q("/whoami");
      let item = await this.context.api.q("/client/get", {
        id: this.state.clientId
      });
      let access = await this.context.api.q("/object/access/decode", {
        id: this.state.clientId,
      });
      let balance = await this.context.api.q("/client/balance", {
        id: this.state.clientId,
      });
      let chart = await this.context.api.q("/transaction/list", {
        fields: ["volume", "amount", "label", "servicecode", "created"],
        search: [{field: "client", value: this.state.clientId},
          {field: "servicecode", value: "charging.service"},
          {field: "created", compare: "GEQ", value: dateFrom},
          {field: "created", compare: "LSS", value: dateTo}]
      })
      let amount = await this.context.api.q("/transaction/list", {
        fields: ["amount"],
        search: [{field: "client", value: this.state.clientId},
          {field: "created", compare: "GEQ", value: dateFrom},
          {field: "created", compare: "LSS", value: dateTo}]
      })
      this.setState({item, access, balance, chart, amount, authResult}, () => this.pendingEnd());
    } else {
      this.setState({item: {}, access: {}, balance: {}, chart: {}, amount: {}, authResult: {}});
    }
  };

  editModalToggle = () => {
    this.setState((state) => {
      return {editModalVisible: !state.editModalVisible};
    });
  };
  editModalFinish = () => {
    this.editModalToggle();
    this.load();
  };
  addModalToggle = () => {
    this.setState((state) => {
      return {addModalVisible: !state.addModalVisible};
    });
  };
  addModalFinish = () => {
    this.addModalToggle();
    this.load();
  };

  render() {
    const {pending, item, access, balance, chart, amount, authResult} = this.state;

    const displayDetailProps = [
      "code",
      "username",
      "firstname",
      "lastname",
      "middlename",
      "email",
      "phone",
      "created",
      "lastupdate",
      "description",
    ];

    const DetailRow = ({item, property}) => {
      let value = item?.[property];
      if (!value) return null;
      if (isDate(value)) value = new Date(value).toLocaleString();
      if (property === "email") value = value ? value[0] || value.default : null;
      if (property === "phone") value = value ? value[0] || value.mobile : null;

      return (
        <Row gutter={[16, 16]} className="gx-mb-2">
          <Col className="gx-text-muted gx-font-weight-light gx-pr-2" xs={10}>
            {this.m(property)}
          </Col>
          <Col xs={14}>{value}</Col>
        </Row>
      );
    };

    const ChartCard = ({chart, amount}) => {

      const [data, setData] = useState(chart);
      const [amountT, setAmount] = useState(amount);
      //const [dates, setDates] = useState([Moment().format("YYYY-MM-DD"), Moment().format("YYYY-MM-DD")]);
      const [range, setRange] = useState([]);
      const [loader, setLoader] = useState(false);

      const onChange = (dates) => {
        setRange(dates);
        const dateTo = dates[1] ? Moment(dates[1]._d).format("YYYY-MM-DD") : Moment().format("YYYY-MM-DD");
        const dateFrom = dates[0] ? Moment(dates[0]._d).format("YYYY-MM-DD") : Moment().format("YYYY-MM-DD");
        this.context.api.q("/transaction/list", {
          fields: ["volume", "amount", "label", "servicecode", "created"],
          search: [{field: "client", value: this.state.clientId},
            {field: "servicecode", value: "charging.service"},
            {field: "created", compare: "GEQ", value: dateFrom},
            {field: "created", compare: "LSS", value: dateTo}]
        }).then(result => setData(result)).catch(e => console.log(e));
        this.context.api.q("/transaction/list", {
          fields: ["amount"],
          search: [{field: "client", value: this.state.clientId},
            {field: "created", compare: "GEQ", value: dateFrom},
            {field: "created", compare: "LSS", value: dateTo}]
        }).then(result => setAmount(result)).catch(e => console.log(e));
      }

      const handleChange = (e) => {
        const value = e.target.value;

        if (value == '0') {
          const dateTo = Moment().format("YYYY-MM-DD");
          const dateFrom = Moment().format("YYYY-MM-DD");
          this.context.api.q("/transaction/list", {
            fields: ["volume", "amount", "label", "servicecode", "created"],
            search: [{field: "client", value: this.state.clientId},
              {field: "servicecode", value: "charging.service"},
              {field: "created", compare: "GEQ", value: dateFrom},
              {field: "created", compare: "LSS", value: dateTo}]
          }).then(result => setData(result)).catch(e => console.log(e));
          this.context.api.q("/transaction/list", {
            fields: ["amount"],
            search: [{field: "client", value: this.state.clientId},
              {field: "created", compare: "GEQ", value: dateFrom},
              {field: "created", compare: "LSS", value: dateTo}]
          }).then(result => setAmount(result)).catch(e => console.log(e));
        } else if (value == '1') {
          const dateTo = Moment().format("YYYY-MM-DD");
          const dateFrom = Moment().subtract(7, 'd').format("YYYY-MM-DD");
          this.context.api.q("/transaction/list", {
            fields: ["volume", "amount", "label", "servicecode", "created"],
            search: [{field: "client", value: this.state.clientId},
              {field: "servicecode", value: "charging.service"},
              {field: "created", compare: "GEQ", value: dateFrom},
              {field: "created", compare: "LSS", value: dateTo}]
          }).then(result => setData(result)).catch(e => console.log(e));
          this.context.api.q("/transaction/list", {
            fields: ["amount"],
            search: [{field: "client", value: this.state.clientId},
              {field: "created", compare: "GEQ", value: dateFrom},
              {field: "created", compare: "LSS", value: dateTo}]
          }).then(result => setAmount(result)).catch(e => console.log(e));
        } else if (value == '2') {
          const dateTo = Moment().format("YYYY-MM-DD");
          const dateFrom = Moment().subtract(30, 'd').format("YYYY-MM-DD");
          this.context.api.q("/transaction/list", {
            fields: ["volume", "amount", "label", "servicecode", "created"],
            search: [{field: "client", value: this.state.clientId},
              {field: "servicecode", value: "charging.service"},
              {field: "created", compare: "GEQ", value: dateFrom},
              {field: "created", compare: "LSS", value: dateTo}]
          }).then(result => setData(result)).catch(e => console.log(e));
          this.context.api.q("/transaction/list", {
            fields: ["amount"],
            search: [{field: "client", value: this.state.clientId},
              {field: "created", compare: "GEQ", value: dateFrom},
              {field: "created", compare: "LSS", value: dateTo}]
          }).then(result => setAmount(result)).catch(e => console.log(e));
        } else if (value == '3') {
          const dateTo = Moment().format("YYYY-MM-DD");
          const dateFrom = Moment().subtract(700, 'd').format("YYYY-MM-DD");
          this.context.api.q("/transaction/list", {
            fields: ["volume", "amount", "label", "servicecode", "created"],
            search: [{field: "client", value: this.state.clientId},
              {field: "servicecode", value: "charging.service"},
              {field: "created", compare: "GEQ", value: dateFrom},
              {field: "created", compare: "LSS", value: dateTo}]
          }).then(result => setData(result)).catch(e => console.log(e));
          this.context.api.q("/transaction/list", {
            fields: ["amount"],
            search: [{field: "client", value: this.state.clientId},
              {field: "created", compare: "GEQ", value: dateFrom},
              {field: "created", compare: "LSS", value: dateTo}]
          }).then(result => setAmount(result)).catch(e => console.log(e));
        } else if (value == '4') {
          const dateTo = range[0] ? Moment(range[0]._d).format("YYYY-MM-DD") : Moment().format("YYYY-MM-DD");
          const dateFrom = range[1] ? Moment(range[1]._d).format("YYYY-MM-DD") : Moment().format("YYYY-MM-DD");
          this.context.api.q("/transaction/list", {
            fields: ["volume", "amount", "label", "servicecode", "created"],
            search: [{field: "client", value: this.state.clientId},
              {field: "servicecode", value: "charging.service"},
              {field: "created", compare: "GEQ", value: dateFrom},
              {field: "created", compare: "LSS", value: dateTo}]
          }).then(result => setData(result)).catch(e => console.log(e));
          this.context.api.q("/transaction/list", {
            fields: ["amount"],
            search: [{field: "client", value: this.state.clientId},
              {field: "created", compare: "GEQ", value: dateFrom},
              {field: "created", compare: "LSS", value: dateTo}]
          }).then(result => setAmount(result)).catch(e => console.log(e));
        }

        setLoader(true);
        setTimeout(() => {
          setLoader(false);
        }, 500);
      };

      return (
        <>
          <div className="ant-row-flex gx-justify-content-between gx-mb-3 gx-dash-search">
            <div className="gx-mx-sm-2">
              <Radio.Group className="gx-radio-group-link gx-radio-group-link-news" defaultValue={2}
                           onChange={handleChange}>
                <Radio.Button value={0} className="gx-mb-0">{this.m("today")}</Radio.Button>
                <Radio.Button value={1} className="gx-mb-0">{this.m("week")}</Radio.Button>
                <Radio.Button value={2} className="gx-mb-0">{this.m("month")}</Radio.Button>
                <Radio.Button value={3} className="gx-mb-0">{this.m("alltime")}</Radio.Button>
                <Radio.Button style={{padding: 0}} value={4} className="gx-mb-1">
                  <RangePicker
                    style={{padding: 10, width: 270}}
                    onChange={(dates) => onChange(dates)}
                  />
                </Radio.Button>
              </Radio.Group>
              <Button
                className="gx-mb-0 gx-mr-0"
                onClick={(e) => {
                  e.stopPropagation();
                  handleChange(e);
                }}
                icon={<RetweetOutlined/>}
              />
            </div>
          </div>
          {loader ? <div><CircularProgress className={"small gx-mt-4"}/></div> :
            <ClientChart intl={this.props.intl} data={data} amount={amountT}/>
          }
        </>
      );
    }
    return (
      <div>
        <Helmet>
          <title>{item.label || this.m("client")}</title>
        </Helmet>
        <h2 className="title gx-mb-4">
          <Link className="gx-link" to="/crm/client">
            {this.m("client")}
          </Link>
          {item?.label && (
            <span>
		<span className="gx-mx-2 gx-text-small">
		  <ArrowRightOutlined/>
		</span>
              {item?.label}
	  </span>
          )}
        </h2>
        <ClientEditModal
          essence="client"
          id={item.id}
          onClose={this.editModalToggle}
          form={clientEditForm.call(this)}
          onFinish={this.editModalFinish}
          visible={this.state.editModalVisible}
        />
        <BalanceEditModal
          essence="client/balance"
          id={item.id}
          client={this.state.clientId}
          onClose={this.addModalToggle}
          form={balanceEditForm.call(this)}
          onFinish={this.addModalFinish}
          visible={this.state.addModalVisible}
        />
        <Spin spinning={pending}>
          {!item?.id && <Empty/>}
          {item?.id && (
            <div className="gx-mb-3">
              <Row gutter={[16, 16]}>
                <Col xl={8} xs={24}>
                  <Card className="gx-h-card">
                    <Flex between className="gx-mb-3">
                      <h4>{this.m("details")}</h4>
                      <Statelabel {...item} onFinish={this.load}/>
                    </Flex>
                      {item.picture ?
                        <Row gutter={[16, 16]} className="gx-mb-2">
                        <div className="gx-flex-row gx-align-items-center gx-flex-nowrap gx-mb-4">
                          <Avatar src={item.picture} className="gx-size-60 gx-pointer gx-mr-3 gx-mb-2" alt="" />
                        </div>
                        <div className="gx-flex-column gx-align-items-left gx-ml-4">
                          {item.fullname && (<div style={{paddingBottom: 10}}>{item.fullname}</div>)}
                          <div>{item.typename}</div>
                        </div>
                        </Row> :
                        <div>
                          <Row gutter={[16, 16]} className="gx-mb-2">
                            <Col className="gx-text-muted gx-font-weight-light gx-pr-2" xs={10}>
                              {this.m("typename")}
                            </Col>
                            <Col xs={14}>{item.typename}</Col>
                          </Row>
                          <Row gutter={[16, 16]} className="gx-mb-2">
                            <Col className="gx-text-muted gx-font-weight-light gx-pr-2" xs={10}>
                              {this.m("name")}
                            </Col>
                            <Col xs={14}>{item.fullname}</Col>
                          </Row>
                        </div>
                      }
                    {displayDetailProps.map((prop) => (
                      <DetailRow key={prop} item={item} property={prop}/>
                    ))}
                    <Row gutter={[16, 16]} className="gx-mt-4 gx-mb-4">
                      <Col className="gx-text-muted gx-font-weight-light gx-pr-2" xs={10}>
                        {this.m("balance")}
                      </Col>
                      <Col
                        xs={14}>{balance == null || balance.length === 0 ? '0' : balance[0].balance ? `${balance[0].balance} ${balance[0].currency}` : '0'}
                      </Col>
                    </Row>
                    {access?.u && (
                      <>
                        <Button
                          onClick={this.editModalToggle}
                          className="gx-mb-0"
                        >
                          {this.m("edit", "")}
                        </Button>
                      </>
                    )}
                    {authResult?.admin && (
                      <>
                        <Button
                          className="gx-mb-0 gx-mr-4"
                          onClick={(e) => {
                            e.stopPropagation();
                            this.addModalToggle();
                          }}
                          icon={<PlusOutlined/>}
                        />
                      </>
                    )}

                  </Card>
                </Col>
                <Col xl={16} xs={24}>
                  <Card className="gx-h-card">
                    <ChartCard chart={chart} amount={amount}/>
                  </Card>
                </Col>
              </Row>
              <Row gutter={[16, 16]} className="gx-mt-3">
                <Col xl={24} xs={24}>
                  <Card className="gx-h-card">
                    <Tabs>
                      <Tabs.TabPane
                        tab={this.m("transactions")}
                        key="transactions"
                      >
                        <h4 className="gx-mb-3">{this.m("transactions")}</h4>
                        <ClientTransactions
                          clientId={item.id}
                          tableItself
                        />
                      </Tabs.TabPane>
                      <Tabs.TabPane
                        tab={this.m("cards")}
                        key="cards"
                      >
                        <h4 className="gx-mb-3">{this.m("cards")}</h4>
                        <ClientCard
                          clientId={item.id}
                          tableItself
                        />
                      </Tabs.TabPane>
                      <Tabs.TabPane
                        tab={this.m("accounts")}
                        key="accounts"
                      >
                        <h4 className="gx-mb-3">{this.m("accounts")}</h4>
                        <ClientAccount
                          clientId={item.id}
                          tableItself
                        />
                      </Tabs.TabPane>
                      <Tabs.TabPane
                        tab={this.m("invoices")}
                        key="invoices"
                      >
                        <h4 className="gx-mb-3">{this.m("invoices")}</h4>
                        <ClientInvoise
                          clientId={item.id}
                          tableItself
                        />
                      </Tabs.TabPane>
                      <Tabs.TabPane
                        tab={this.m("orders")}
                        key="orders"
                      >
                        <ClientOrder
                          access={authResult.admin}
                          clientId={item.id}
                          tableItself
                        />
                      </Tabs.TabPane>
                      <Tabs.TabPane
                        tab={this.m("stations")}
                        key="stations"
                      >
                        <h4 className="gx-mb-3">{this.m("stations")}</h4>
                        <ClientStations
                          clientId={item.id}
                          tableItself
                        />
                      </Tabs.TabPane>
                    </Tabs>
                  </Card>
                </Col>
              </Row>
            </div>
          )}
        </Spin>
      </div>

    )
  }
}

export default withRouter(injectIntl(ClientDetail));
