import React from "react";
import {Form, Button, message, Modal, Spin, Empty, Input} from "antd";
import Context from "../../Context";
import {injectIntl} from "react-intl";

class ClientEditModal extends React.Component {
  static contextType = Context;

  constructor(props) {
    super(props);
    this.state = {
      loadPending: false,
      savePending: false,
      initialValues: {},
    };
  }

  pendingStart = () => {
    if (this.props.onPendingStart) {
      this.props.onPendingStart();
      return;
    }
    this.setState({loadPending: true});
  };
  pendingEnd = () => {
    if (this.props.onPendingEnd) {
      this.props.onPendingEnd();
      return;
    }
    this.setState({loadPending: false});
  };

  componentDidMount() {
    this.load();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.id !== this.props.id) {
      this.load();
    }
  }

  load = async () => {
    if (this.props.id) {
      this.pendingStart();
      let queryParams = this.props.queryParams || {id: this.props.id};
      let loadPath = this.props.loadPath || `/${this.props.essence}/get`;
      let queryResult = await this.context.api.q(loadPath, queryParams, {
        exact: true,
      });

      if (queryResult.email) {
        queryResult.email = queryResult.email[0] || queryResult.email.default || null;
      }

      if (queryResult.phone) {
        queryResult.phone = queryResult.phone[0] || queryResult.phone.mobile || null;
      }

      this.setState({initialValues: queryResult || {}}, () =>
        this.pendingEnd()
      );
    } else {
      this.setState({initialValues: {}});
    }
  };

  save = async (data) => {
    let item = this.state.initialValues;
    let prepareData = this.props.prepareData
      ? this.props.prepareData({data, item})
      : data;

    let savePath = this.props.savePath || `/${this.props.essence}/set`;

    this.setState({savePending: true});

    let saveData = {};

    saveData.id = item.id || null;
    saveData.type = prepareData.type;
    saveData.code = prepareData.code;
    saveData.email = [prepareData.email];
    saveData.phone = {"mobile": prepareData.phone};
    saveData.name = {"first": prepareData.firstname, "last": prepareData.lastname, "middle": prepareData.middlename};
    saveData.description = prepareData.description;

    let result = await this.context.api.q(savePath, saveData);
    if (!result) {
      message.error(this.m("save.fail", ""));
      this.setState({pending: false});
    } else {
      message.success(this.m("save.success", ""));
      if (this.props.onFinish) this.props.onFinish();
    }

    this.setState({savePending: false});
  };

  onClose = () => {
    if (this.props.onClose) this.props.onClose();
  };

  m = (key, prefix = "editmodal.") => {
    const {intl} = this.props;
    return intl.formatMessage({id: prefix + key});
  };

  render() {
    const {visible, essence, id} = this.props;
    const {loadPending, savePending, initialValues} = this.state;

    return (
      <Modal visible={visible} footer={null} onCancel={this.onClose}>
        <Spin spinning={loadPending}>
          {!loadPending && id && !initialValues.id ? (
            <Empty/>
          ) : (
            <div className="gx-p-3">
              <Form
                key={initialValues.id}
                name={essence}
                layout="vertical"
                initialValues={initialValues}
                onFinish={this.save}
                scrollToFirstError
              >
                {this.props.form}
                <Button
                  loading={savePending}
                  type="primary"
                  htmlType="submit"
                  className="gx-mb-0"
                >
                  {this.m("save", "")}
                </Button>
              </Form>
            </div>
          )}
        </Spin>
      </Modal>
    );
  }
}

export default injectIntl(ClientEditModal);
