import React from "react";
import Context from "../../Context";
import {injectIntl} from "react-intl";
import key from "../../util/key";

import {Table} from "antd";

class InvoiceTransactions extends React.Component {
  static contextType = Context;

  constructor(props) {
    super(props);
    this.state = {
      tableUuid: key(),
      columns: [],
    };
  }

  m = (key, prefix = "invoice.transactions.") => {
    const {intl} = this.props;
    return intl.formatMessage({id: prefix + key});
  };

  componentDidMount() {
    this.setColumns();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.setColumns();
    }
  }

  setColumns = () => {
    this.setState({
      columns: [
        {
          width: "25%",
          text: true,
          title: this.m("label"),
          dataIndex: "label",

          render: (value, item) => <> {value} </>,
        },
        {
          width: "25%",
          title: this.m("tariff"),
          dataIndex: "tariffname",
          sorter: (a, b) => a.price - b.price,
          render: (value) => <>{value}</>,
        },
        {
          width: "15%",
          title: this.m("price"),
          dataIndex: "price",
          sorter: (a, b) => a.price - b.price,
          render: (value) => <>{value}</>,
        },
        {
          width: "15%",
          title: this.m("volume"),
          dataIndex: "volume",
          render: (value) => <>{value}</>,
          sorter: (a, b) => a.volume - b.volume,
        },
        {
          width: "15%",
          title: this.m("amount"),
          dataIndex: "amount",
          render: (value) => <>{value}</>,
          sorter: (a, b) => a.volume - b.volume,
        },
      ],
    });
  };

  render() {
    return (
      <Table
        style={{width: "600px"}}
        scroll={{x: 400}}
        showSorterTooltip={false}
        columns={this.state.columns}
        rowKey={(record) => record.id}
        dataSource={this.props.item?.transactions || []}
      />
    );
  }
}

export default injectIntl(InvoiceTransactions);
