import {Col, Row} from "antd";
import React from "react";
import {Bar, BarChart, ResponsiveContainer, Tooltip,} from "recharts";

import Widget from "../../components/Widget/index";

const ClientChart = ({data, amount, intl}) => {
  const m = (key, prefix = "client.") => {

    return intl.formatMessage({id: prefix + key});
  };
  data.forEach(element => {
    return {name: element.created, volume: element.volume}
  });

  const sumVol = data.reduce(
    (accumulator, currentValue) => accumulator + currentValue.volume,
    0,
  );
  const sumAmount = amount.reduce(
    (accumulator, currentValue) => accumulator + currentValue.amount,
    0,
  );
  return (
    <Widget>
      <div className="gx-d-flex">

      </div>
      <Row>
        <Col lg={10} md={12} sm={24} xs={24} className="gx-pr-2">
          <div className="gx-pt-2">
            <h2 className="gx-fs-xxxl gx-font-weight-medium gx-mb-1 gx-text-black">{`${sumVol.toFixed(2)} kWh`}</h2>
            <p className="gx-mb-2 gx-mb-md-0">{m("energy")}</p>
          </div>
          <div className="gx-pt-2">
            <h2 className="gx-fs-xxxl gx-font-weight-medium gx-mb-1 gx-text-black">{`${sumAmount.toFixed(2)}`}</h2>
            <p className="gx-mb-2 gx-mb-md-0">{m("profit")}</p>
          </div>
        </Col>
        <Col lg={14} md={12} sm={24} xs={24}>
          <ResponsiveContainer width="100%" height={160}>
            <BarChart data={data}
                      margin={{top: 0, right: 0, left: 0, bottom: 0}}>
              <Tooltip/>
              <defs>
                <linearGradient id="color08" x1="0" y1="1" x2="0" y2="0">
                  <stop offset="5%" stopColor="#23DFDC" stopOpacity={0.9}/>
                  <stop offset="95%" stopColor="#63AEE4" stopOpacity={0.9}/>
                </linearGradient>
              </defs>
              <Bar dataKey="volume" fill="url(#color08)" barSize={10}/>
            </BarChart>
          </ResponsiveContainer>
        </Col>
      </Row>
    </Widget>
  );
};

export default ClientChart;
