import React from "react";
import { useSelector } from "react-redux";
import PlainSelect from "../../util/PlainSelect";
import { Form, Input, InputNumber } from "antd";
const { TextArea } = Input;

export default function orderEditForm() {
	const config = window.appConfig || {};

  let formItems = {
    currency: {
      rules: [
        {
          required: true,
        },
      ],
	  label: this.m("currency"),
      component: (
        <PlainSelect labelKey="label" cache query="/currency/list" />
      ),
    },
    amount: {
      rules: [
        {
          required: true,
		  type: 'integer',
        },
      ],
	  //component: ( <InputNumber/>),
    },
    description: {
		component: <TextArea />,
	  },
  };
 
  return (
    <>
      {Object.entries(formItems).map(([key, item]) => {
        let label = item.label || this.m(key);
        let component = item.component || <Input />;

        return (
          <Form.Item key={key} rules={item.rules} label={label} name={key}>
            {component}
          </Form.Item>
        );
      })}
    </>
  );
}