import React from "react";
import Context from "../../Context";
import { injectIntl } from "react-intl";
import key from "../../util/key";
import { SearchOutlined } from "@ant-design/icons";
import DataTable from "../../components/Datatable/DataTable";
import DataTablePeriod from "../../components/Datatable/DataTablePeriod";
import DataTableSearch from "../../components/Datatable/DataTableSearch";
import { Helmet } from "react-helmet";
import { Link } from "react-router-dom";
import Statelabel from "../../util/Statelabel";

class ClientTransactions extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      tableUuid: key(),
      columns: [],
    };
  }
  m = (key, prefix = "client.transactions.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };
  componentDidMount() {
    this.setColumns();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.setColumns();
    }
  }

  onMethodFinish = () => {
    this.setState({
      tableUuid: key(),
    });
    if (this.props.onMethodFinish) {
      this.props.onMethodFinish();
    }
  };

  setColumns = () => {
    this.setState({
      columns: [
        {
          text: true,
          title: this.m("identity"),
          dataIndex: "identity",
          sorter: true,
          width: "15%",
          render: (value, item) => (
            <>
              <Link to={`/crm/station/${item.chargepoint}`}>{value}</Link>
              {item.connectorid && (
                <div className="gx-text-muted gx-font-weight-light">
                  {item.connectorid}
                </div>
              )}
            </>
          ),
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },
        {
          text: true,
          title: this.m("invoicecode"),
          dataIndex: "invoicecode",
          sorter: true,
          width: "10%",
          render: (value) => <>{value}</>,
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },
        {
          text: true,
          title: this.m("servicename"),
          dataIndex: "servicename",
          sorter: true,
          width: "15%",
          render: (value) => <>{value}</>,
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },
        {
          text: true,
          title: this.m("tariffname"),
          dataIndex: "tariffname",
          sorter: true,
          width: "15%",
          render: (value, item) => <>
            <div>{item.tariffname}</div>

            {item.tariffdescription && (
              <div className="gx-font-weight-light gx-text-small gx-text-muted">
                {item.tariffdescription}
              </div>
            )} </>,
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },
        {
          text: true,
          title: this.m("currency"),
          dataIndex: "currencycode",
          sorter: true,
          width: "10%",
          render: (value, item) => <>
            <div>{item.currencycode} ({item.currencyname})</div>

            {item.currencydescription && (
              <div className="gx-font-weight-light gx-text-small gx-text-muted">
                {item.currencydescription}
              </div>
            )} </>,
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },
        {
          title: this.m("price"),
          dataIndex: "price",
          sorter: true,
          width: "5%",
        },
        {
          title: this.m("volume"),
          dataIndex: "volume",
          sorter: true,
          width: "5%",
        },
        {
          title: this.m("amount"),
          dataIndex: "amount",
          sorter: true,
          width: "5%",
        },
        {
          title: this.m("created"),
          dataIndex: "created",
          sortDirections: "descend",
          defaultSortOrder: true,
          sorter: true,
          width: "5%",
          render: (value) => {
            if (value) {
              const dt = new Date(value);
              return (
                <>
                  {dt.toLocaleDateString()}
                  <br/>
                  {dt.toLocaleTimeString()}
                </>
              );
            }
          },

          date: true,
          filterDropdown: (props) => <DataTablePeriod {...props} />,
        },
        {
          width: "5%",
          title: this.m("statename"),
          dataIndex: "state",
          sorter: true,
          render: (value, item) => (
            <div className="gx-text-right">
              <Statelabel onFinish={this.onMethodFinish} {...item} />
            </div>
          ),
        },
      ],
    });
  };

  render() {
    const forceFilter = this.props.clientId
      ? { client: this.props.clientId }
      : null;

    const tableItself = (
      <DataTable
        forceFilter={forceFilter}
        uuid={this.state.tableUuid}
        columns={this.state.columns}
        path={"/transaction/list"}
      />
    );
    if (this.props.tableItself) {
      return tableItself;
    }
    return (
      <div>
        <Helmet>
          <title>{this.m("transactions")}</title>
        </Helmet>
        <h2 className="title gx-mb-4">{this.m("transactions")}</h2>
        {tableItself}
      </div>
    );
  }
}

export default injectIntl(ClientTransactions);
