import React from "react";
import Context from "../../Context";
import {injectIntl} from "react-intl";
import key from "../../util/key";
import {PlusOutlined, SearchOutlined} from "@ant-design/icons";
import DataTable from "../../components/Datatable/DataTable";
import DataTableSearch from "../../components/Datatable/DataTableSearch";
import Statelabel from "../../util/Statelabel";
import {Button} from "antd";
import EditModal from "../../util/EditModal";
import cardEditForm from "../Card/cardEditForm";
import DataTablePeriod from "../../components/Datatable/DataTablePeriod";

class ClientCard extends React.Component {
  static contextType = Context;

  constructor(props) {
    super(props);
    this.state = {
      tableUuid: key(),
      columns: [],

      editModalVisible: false,
      editModalId: null,
    };
  }

  m = (key, prefix = "card.") => {
    const {intl} = this.props;
    return intl.formatMessage({id: prefix + key});
  };

  componentDidMount() {
    this.setColumns();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.setColumns();
    }
  }

  onMethodFinish = () => {
    this.setState({
      tableUuid: key(),
    });
    if (this.props.onMethodFinish) {
      this.props.onMethodFinish();
    }
  };

  editModalToggle = (editModalId = null) => {
    this.setState((state) => {
      return {editModalVisible: !state.editModalVisible, editModalId};
    });
  };

  editModalFinish = () => {
    this.editModalToggle();
    this.onMethodFinish();
  };

  setColumns = () => {
    this.setState({
      columns: [
        {
          text: true,
          title: (
            <>
              <Button
                className="gx-mr-4"
                onClick={(e) => {
                  e.stopPropagation();
                  this.editModalToggle();
                }}
                icon={<PlusOutlined/>}
              />
              {this.m("identity")}
            </>
          ),
          dataIndex: "label",
          sorter: true,
          width: "15%",
          render: (value, item) => (
            <>
              <div
                className="gx-pointer gx-text-primary"
                onClick={() => this.editModalToggle(item.id)}
              >
                <div>{value}</div>
                <div>{item.code}</div>

                {item.description && (
                  <div className="gx-font-weight-light gx-text-small gx-text-muted">
                    {item.description}
                  </div>
                )}
              </div>
            </>
          ),

          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },

        {
          width: "15%",
          title: this.m("typename"),
          dataIndex: "typecode",
          sorter: true,
          render: (value, item) => <>{item.typename}</>,
          filters: [
            {
              text: this.m("type.rfid"),
              value: "rfid.card",
            },
            {
              text: this.m("type.credit"),
              value: "credit.card",
            },
            {
              text: this.m("type.plastic"),
              value: "plastic.card",
            },
          ],
        },

        {
          text: true,
          title: this.m("name"),
          dataIndex: "name",
          sorter: true,
          width: "15%",
          render: (value, item) => <>
            <div>{item.name}</div>
          </>,
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },

        {
          width: "10%",
          title: this.m("expiry"),
          dataIndex: "expiry",
          sorter: true,
          render: (value) => {
            if (value) {
              const dt = new Date(value);
              return (
                <>
                  {dt.toLocaleDateString()}
                  <br/>
                  {dt.toLocaleTimeString()}
                </>
              );
            }
          },

          date: true,
          filterDropdown: (props) => <DataTablePeriod {...props} />,
        },

        {
          width: "10%",
          title: this.m("created"),
          dataIndex: "created",
          sortDirections: "descend",
          defaultSortOrder: true,
          sorter: true,
          render: (value) => {
            if (value) {
              const dt = new Date(value);
              return (
                <>
                  {dt.toLocaleDateString()}
                  <br/>
                  {dt.toLocaleTimeString()}
                </>
              );
            }
          },

          date: true,
          filterDropdown: (props) => <DataTablePeriod {...props} />,
        },

        {
          width: "5%",
          title: this.m("statename"),
          dataIndex: "state",
          sorter: true,
          render: (value, item) => (
            <div className="gx-text-right">
              <Statelabel onFinish={this.onMethodFinish} {...item} />
            </div>
          ),
        },
      ],
    });
  };

  render() {
    const forceFilter = this.props.clientId
      ? {client: this.props.clientId}
      : null;
    return (
      <div>
        <EditModal
          essence="card"
          onClose={this.editModalToggle}
          form={cardEditForm.call(this)}
          onFinish={this.editModalFinish}
          visible={this.state.editModalVisible}
          id={this.state.editModalId}
        />
        <DataTable
          forceFilter={forceFilter}
          uuid={this.state.tableUuid}
          columns={this.state.columns}
          path={"/card/list"}
        />
      </div>
    );
  }
}

export default injectIntl(ClientCard);
